  justify-content: center;
  align-items: center;
  flex-direction: column;
}
img {
  background-color: rgb(255, 255, 255);
  width: 70% !important;
  height: 100% !important;
  
  // height: 300px;
}
.carousel .slide {
  background-color: white !important;
}

